import React from "react"
import pym from "pym.js"

function Social() {

  let urlObj = {}
  var pymChild = new pym.Child();
  pymChild.onMessage('loaded', onLoadedMessage);
  function onLoadedMessage(url) {
      urlObj.url = url
  }

  // let url = 'https://www.delawareonline.com/in-depth/news/2020/10/07/delaware-statewide-general-governor-senate-house-election-voters-guide/5907934002/'
  let encoded = encodeURI(urlObj.url)

  return(<div className="socialIcons padding-bottom-1">
            <a
              href={`https://www.facebook.com/sharer/sharer.php?u=${encoded}`}>
              <i className="fab fa-facebook-f" style={{color:"#4961A7"}}></i>
            </a>
            <a
              href={`https://twitter.com/intent/tweet?url=${encoded}`}
              target="_blank" rel="noopener noreferrer" data-popup="" rel="nofollow">
              <i className="fab fa-twitter" style={{color:"#2EB2E8"}}></i>
            </a>
            <a
              href={`mailto:?subject=${encodeURI('Delaware Online Voters Guide')}&body=${encoded}`}>
              <i className="fas fa-envelope" style={{color:"#8A8989"}}></i>
            </a>
          </div>)
}

export default Social
